import React, { useState, useEffect } from "react";
import Grid from "@mui/material/Grid";
import Typography from "@mui/material/Typography";
import ComplexNav from "./nav/ComplexNav";
import useWindowWidthSize from "../hooks/useWindowWidthSize";
import { Switch, Route, useHistory } from "react-router-dom";
import MainBody from "./bodies/MainBody";
import FOFBody from "./bodies/FOFBody";
import ProductBody from "./bodies/ProductBody";

const locas = ["", "orgsrh", "addser", "shop", "polsrh"];

const MainPage: React.VFC = () => {
    const [tabIdx, setTabIdx] = useState(0);
    const handleTabChange = (event: React.SyntheticEvent, newValue: number) => {
        setTabIdx(newValue);
        handleNavClick(newValue);
    };
    const winWidth = useWindowWidthSize();

    const history = useHistory();

    const handleNavClick = (idx: number) => {
        history.push(`/${locas[idx]}`);
    };

    return (
        <>
            {/* navigation */}
            <ComplexNav
                winWidth={winWidth}
                value={tabIdx}
                handleChange={handleTabChange}
                locas={locas}
            />
            <Grid container spacing={2}>
                <Grid item sx={{ width: "100%" }}></Grid>
                {/* mainbody */}
                <Switch>
                    <Route path="/" exact>
                        <MainBody />
                    </Route>
                    <Route path="/orgsrh">
                        <div>기관검색</div>
                    </Route>
                    <Route path="/addser">
                        <div>서비스신청</div>
                    </Route>
                    <Route path="/shop">
                        <ProductBody />
                    </Route>
                    <Route path="/polsrh">
                        <div>정책구매</div>
                    </Route>
                    <Route path="/auth">
                        <div>인증</div>
                    </Route>
                    <Route>
                        <FOFBody />
                    </Route>
                </Switch>
            </Grid>
        </>
    );
};

export default MainPage;
